*{
  box-sizing: border-box;
}

.App {
  // background-color: #f1f3f6;
  .grid_container {
    display: grid;
    width: 100%;
    grid-template-columns: 100%;
    position: relative;
  }
}

@media (min-width: 1051px) {
  .App {
    .grid_container {
      width: 992px;
      grid-template-columns: 25.38% 74.61%;
      // background-color: white;
    }
  }
}

@media (min-width: 1200px) {
  .App {
    .grid_container {
      width: 1140px;
    }
  }
}

@media (min-width: 1400px) {
  .App {
    .grid_container {
      width: 1320px;
    }
  }
}

@media (min-width: 1600px) {
  .App {
    .grid_container {
      width: 1493px;
    }
  }
}
