.recent-products {
  display: flex;
  flex-direction: column;
  align-items: center;

  .recent-products__title {
    margin: 30px 0px 30px;
    font-size: 25px;
    font-weight: 700;
  }
  .recent-products__container {
    max-width: 1500px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    row-gap: 20px;
    column-gap: 20px;
    // border: 2px solid black;
    // height: 50vh;
    // flex-wrap: wrap;
    // padding: 20px;
    // margin-left:auto !important;
    .card {
      align-self: center;
      justify-self: center;
    }

    // .error_msg{
    //   grid-column: 1/5;
    //   font-size: 50px;
    //   font-weight: 700;
    //   align-self: center;
    //   justify-self: center;
    // }
  }
}

@media (max-width: 1600px) {
  .recent-products {
    .recent-products__container {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }
  }
}

@media (max-width: 1200px) {
  .recent-products {
    .recent-products__container {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
}

@media (max-width: 900px) {
  .recent-products {
    .recent-products__container {
      grid-template-columns: 1fr 1fr;
    }
  }
}

@media (max-width: 576px) {
  .recent-products {
    .recent-products__title {
      font-size: 20px;
    }
    .recent-products__container {
      grid-template-columns: 1fr;
    }
  }
}
