.product-details {
  // height: 80vh;
  width: 100vw;
  display: grid;
  grid-template-columns: 1fr 1fr;
  // border: 2px solid black;

  .product-details__image {
    // height: 100%;
    width: 40vw;
    padding: 50px 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    // border: 2px solid black;

    img {
      height: 600px;
      width: 600px;
      // border: 2px solid black;
      // border-radius: 10px !important;
      object-fit: contain;
    }
  }
  .product-details__description {
    // height: 100%;
    width: 60vw;
    padding: 50px 30px;
    display: flex;
    flex-direction: column;
    row-gap: 10px;
    // border: 2px solid black;

    .product-details__name{
      font-size: 25px;
    }
    .product-details__pricetitle{
      font-size: 13px;
      font-weight:500;
      color: green;
    }
    .product-details__price{
      font-size: 30px;
      font-weight: 700;
    }
    .product-details__rating,.product-details__reviewcount{
      font-weight: 500;
      color: white;
      margin: 0px 5px;
      padding: 3px;
      border-radius: 3px;
      background-color: green;
    }
  }
}

@media (max-width: 1600px) {
  .product-details {
    .product-details__image {
      img {
        height: 500px;
        width: 500px;
      }
    }
  }
}

@media (max-width: 1400px) {
  .product-details {
    .product-details__image {
      width: 45vw;
    }
    .product-details__description {
      width: 55vw;
    }
  }
}

@media (max-width: 1200px) {
  .product-details {
    .product-details__image {
      img {
        height: 400px;
        width: 400px;
      }
      // img {
      //   height: 350px;
      //   width: 350px;
      // }
    }
  }
}

@media (max-width: 992px) {
  .product-details {
    .product-details__image {
      img {
        height: 300px;
        width: 300px;
      }
    }
  }
}

@media (max-width: 768px) {
  .product-details {
    grid-template-columns: 1fr;
    .product-details__image {
      width: 100%;
      padding: 30px 50px;
      img {
        height: 400px;
        width: 400px;
      }
    }
    .product-details__description {
      width: 100%;
      padding: 0px 50px 30px;
    }
  }
}

@media (max-width: 576px) {
  .product-details {
    .product-details__image {
      img {
        height: 300px;
        width: 300px;
      }
    }
  }
}

@media (max-width: 420px) {
  .product-details {
    .product-details__image {
      padding: 30px 0px;
      img {
        height: 300px;
        width: 300px;
      }
    }
    .product-details__description {
      padding: 0px 20px 30px;
    }
  }
}
